(function () {
  'use strict';

  angular
    .module('ionic-cache-src')
    .directive('cacheSrc', cacheSrcDirective);

  function cacheSrcDirective($ionicPlatform, $interval, $compile, $cacheSrc, $cordovaFileTransfer, $localStorage) {
    return {
      restrict: 'A',
      scope: {
        'onProgress': '=?',
        'onFinish': '=?',
        'onError': '=?'
      },
      link: function (scope, element, attrs) {

        function id() {
          var text = "";
          var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
          for (var i = 0; i < 16; i++)
            text += possible.charAt(Math.floor(Math.random() * possible.length));
          return text;
        };

        function makeProgressCircle($scope, $compile) {
          return angular.element($compile('<div style="text-align:{{textAlign}}"><div round-progress  max="max"  current="progress"  color="{{color}}" bgcolor="{{bgcolor}}"  radius="{{radius}}"  stroke="{{stroke}}"  rounded="rounded" clockwise="clockwise" iterations="{{iterations}}"  animation="{{animation}}"></div></div>')($scope));
        };

        function startsWith(str, arr) {
          for (var i = 0; i < arr.length; i++) {
            var sub_str = arr[i];
            if (str.indexOf(sub_str) === 0) {
              return true;
            }
          }
          return false;
        };


        function needDownload(path) {
          if (startsWith(path, [
              'http://',
              'https://',
              'ftp://'
            ])) {
            return true;
          } else {
            return false;
          }
        }

        function attrToScope(scope, attrs) {
          scope.progress = 0;
          scope.max = 100;
          scope.radius = attrs.radius;
          scope.stroke = attrs.stroke;
          scope.animation = attrs.animation;
          scope.clockwise = attrs.clockwise;
          scope.color = attrs.color;
          scope.bgcolor = attrs.bgcolor;
          scope.rounded = attrs.rounded;
          scope.iterations = attrs.iterations;
          scope.textAlign = 'center';
        }

        var isIOS = ionic.Platform.isIOS();
        var isAndroid = ionic.Platform.isAndroid();

        if (window.cordova & isIOS || isAndroid) {

          var progress_circle;
          var display;
          var config = {};
          angular.extend(config, $cacheSrc);
          angular.extend(config, attrs);
          attrToScope(scope, config);
          scope.onProgress = scope.onProgress || function () {};
          scope.onFinish = scope.onFinish || function () {};
          scope.onError = scope.onError || function () {};
          var cache = $localStorage.cache_src = $localStorage.cache_src || {};
          //**********//
          var finish = function (result) {
            if (config.showProgressCircleInDevice) {
              element.css('display', display);
              progress_circle.remove();
            }
            addSrc(result);
          };

          var addSrc = function (result) {
            element[0][config.srcIs || 'src'] = result;
            scope.onFinish(result);
          };


          attrs.$observe('cacheSrc',
            function () {
              if (attrs.cacheSrcDirective) {
                if (needDownload(attrs.cacheSrcDirective)) {
                  //if cached
                  if (cache[attrs.cacheSrcDirective]) {
                    $ionicPlatform
                      .ready()
                      .then(function () {
                        addSrc(getCacheDir() + cache[attrs.cacheSrcDirective]);
                      });
                  } else {
                    // not cache
                    if (config.showProgressCircleInDevice) {
                      progress_circle = makeProgressCircle(scope, $compile);
                      display = element.css('display');
                      element.css('display', 'none');
                      element.after(progress_circle);
                    }
                    $ionicPlatform
                      .ready()
                      .then(function () {
                        var ext = '.' + attrs.cacheSrcDirective.split('.').pop();
                        var fileName = id() + ext;
                        $cordovaFileTransfer
                          .download(attrs.cacheSrcDirective, getCacheDir() + fileName, {}, true)
                          .then(function (result) {
                            cache[attrs.cacheSrcDirective] = fileName;
                            finish(getCacheDir() + fileName);
                          }, scope.onError, function (progress) {
                            scope.progress = (progress.loaded / progress.total) * 100;
                            scope.onProgress(scope.progress);
                          });
                      });
                  }
                } else {
                  addSrc(attrs.cacheSrcDirective);
                }
              }
            });
        } else {
          // in browser
          var progress_circle = makeProgressCircle(scope, $compile);
          var config = {};
          angular.extend(config, $cacheSrc);
          angular.extend(config, attrs);
          attrToScope(scope, config);
          scope.onProgress = scope.onProgress || function () {};
          scope.onFinish = scope.onFinish || function () {};
          attrs.$observe('cacheSrc', function () {
            if (attrs.cacheSrcDirective) {
              if (needDownload(attrs.cacheSrcDirective)) {
                if (config.showProgressCircleInBrowser) {
                  var display = element.css('display');
                  element.css('display', 'none');
                  element
                    .after(progress_circle);
                }
                var promise = $interval(function () {
                  scope.progress += 10;
                  scope.onProgress(scope.progress);
                  if (scope.progress == 100) {
                    $interval.cancel(promise);
                    if (config.showProgressCircleInBrowser) {
                      element.css('display', display);
                      progress_circle.remove();
                    }
                    element[0][config.srcIs || 'src'] = attrs.cacheSrcDirective;
                    scope.onFinish(attrs.cacheSrcDirective);
                  }
                }, config.interval);
              } else {
                element[0][config.srcIs || 'src'] = attrs.cacheSrcDirective;
                scope.onFinish(attrs.cacheSrcDirective);
              }
            }
          });
        }

      }
    };
  }

  function getCacheDir() {
    switch (window.device.platform) {
    case 'iOS':
      return cordova.file.documentsDirectory;
    case 'Android':
      return cordova.file.dataDirectory;
    }
    return '';
  }
})();

(function () {
  'use strict';

  angular
    .module('ionic-cache-src')
    .factory('cacheSrcStorage', cacheSrcStorageFactory);

  function cacheSrcStorageFactory($localStorage) {
    var c = {};
    c._cache = $localStorage.cache_src;
    c.get = function (url) {
      return c._cache[url] && (getCacheDir() + c._cache[url]);
    };
    c.set = function (url, localUrl) {
      c._cache[url] = localUrl;
      return c;
    };
    return c;
  }
})();

(function () {
    'use strict';

    /**
     * @ngdoc directive
     * @name modalLanguage
     *
     * @description
     * _Please update the description and restriction._
     *
     * @restrict A
     * */

    var path = 'app/component/ion-language';

    angular
        .module('ion-language')
        .directive('ionLanguage', ionLanguageDirective);

    function ionLanguageDirective(AppConfig, $translate, $timeout, Loading, $ionicModal) {
        return {
            restrict: 'E',
            link: modalLanguageLink,
            templateUrl: path + '/ion-language.html'
        };

        function modalLanguageLink(scope, elem, attr) {


            scope.languages = AppConfig.locales;
            scope.language  = scope.languages.filter(function (item) {
                return item.code === $translate.use()
            })[0];

            console.log(scope.language);

            elem.bind('click', openModal);

            function openModal() {

                scope.closeModal = function () {
                    scope.modal.hide();
                    // scope.modal.remove();
                };


                scope.searchValue = '';

                scope.selectLanguage = function (language) {
                    console.log(language);
                    $translate.use(language.code);
                    moment.locale(language.code);
                    scope.language = language;
                    Loading.start();
                    scope.closeModal();
                    $timeout(function () {
                        Loading.end();
                    }, 1000);
                };

                $ionicModal
                    .fromTemplateUrl(path + '/modal-language.html', {
                        scope: scope,
                        focusFirstInput: true
                    })
                    .then(function (modal) {
                        scope.modal = modal;
                        scope.modal.show();

                    });
            }
        }
    }

})();

(function () {
  'use strict';

  angular
    .module('ionic-loading')
    .factory('Loading', Loading);

  function Loading($rootScope, $timeout) {
    var seconds = 0;

    return {
      start: showLoading,
      end: hideLoading,
    };


    function showLoading(text) {
      $rootScope.$broadcast('ionicLoading:true', text);
    }

    function hideLoading() {
      $timeout(function () {
        $rootScope.$broadcast('ionicLoading:false');
      }, parseInt(seconds + '000'));
    }
  }
})();
