(function () {
    'use strict';

    angular
        .module('ion-photo')
        .factory('PhotoService', PhotoServiceFactory);

    function PhotoServiceFactory($ionicActionSheet, $translate, AppConfig, PhotogramShare, $jrCrop, $rootScope, $ionicModal,
                          $cordovaCamera, $cordovaCapture, $q) {

        // Default Setting
        var tempImage;
        var cordova = window.cordova;
        var setting = {
            jrCrop: true,
            quality: 90,
            allowEdit: false,
            filter: true,
            correctOrientation: true,
            targetWidth: 300,
            targetHeight: 300,
            saveToPhotoAlbum: false,
            allowRotation: false,
            aspectRatio: 0
        };

        return {
            open: openModal,
            crop: cropModal,
            filter: filterModal,
            share: shareModal,
        };

        function openModal(option) {
            var defer       = $q.defer();
            var options     = {
                jrCrop: option.jrCrop ? option.jrCrop : setting.jrCrop,
                filter: option.filter ? option.filter : setting.filter,
                quality: option.quality ? option.quality : setting.quality,
                aspectRatio: option.aspectRatio ? option.aspectRatio : setting.aspectRatio,
                allowRotation: option.allowRotation ? option.allowRotation : setting.allowRotation,
                allowEdit: option.allowEdit ? option.allowEdit : setting.allowEdit,
                correctOrientation: option.correctOrientation ? option.correctOrientation : setting.correctOrientation,
                targetWidth: option.width ? option.width : setting.targetWidth,
                targetHeight: option.height ? option.height : setting.targetHeight,
                saveToPhotoAlbum: option.saveToPhotoAlbum ? option.saveToPhotoAlbum : setting.saveToPhotoAlbum,
                destinationType: window.cordova ? Camera.DestinationType.DATA_URL : null,
                encodingType: window.cordova ? Camera.EncodingType.JPEG : null,
                popoverOptions: window.cordova ? CameraPopoverOptions : null,
            };
            var buttons     = [{
                text: '<i class="icon ion-ios-camera"></i>' + $translate.instant('ION-PHOTO.CAMERA')
            }, {
                text: '<i class="icon ion-images"></i>' + $translate.instant('ION-PHOTO.GALLERY')
            },
                // {text: '<i class="icon ion-ios-videocam"></i>' + $translate.instant('ION-PHOTO.VIDEO')}
            ];
            var actionSheet = $ionicActionSheet.show({
                buttons: buttons,
                titleText: $translate.instant('ION-PHOTO.SHARE'),
                cancelText: $translate.instant('ION-PHOTO.CANCEL'),
                cancel: buttonCancel,
                buttonClicked: buttonClicked
            });

            function buttonClicked(index) {
                console.log(index);
                actionSheet();
                capture(index, options)
                    .then(cropModal)
                    .then(filterModal)
                    .then(function (resp) {
                        console.log('Final rest');
                        defer.resolve(resp);
                    })
                    .catch(buttonCancel);
            }


            function buttonCancel(resp) {
                actionSheet(resp);
            }

            return defer.promise;
        }

        function cropModal(image) {
            var defer = $q.defer();

            if (setting.jrCrop) {
                $jrCrop
                    .crop({
                        url: image,
                        aspectRatio: setting.aspectRatio ? setting.aspectRatio : false,
                        allowRotation: setting.allowRotation ? setting.allowRotation : false,
                        width: setting.width ? setting.width : setting.targetWidth,
                        height: setting.height ? setting.height : setting.targetHeight,
                        cancelText: $translate.instant('ION-PHOTO.CANCEL'),
                        chooseText: $translate.instant('ION-PHOTO.CROP')
                    })
                    .then(function (canvas) {
                        defer.resolve(canvas.toDataURL());
                    })
                    .catch(defer.reject);
            } else {
                defer.resolve(image);
            }
            return defer.promise;
        }

        function shareModal(image) {
            var template =
                    '<ion-modal-view class="modal-share"> <ion-header-bar class="bar-dark"> <div class="title">{{ \'ION-PHOTO.SHARE\' | translate }}</div> <button class="button button-positive" ng-click="modal.hide()"> <i class="icon ion-arrow-right-b"></i> </button> </ion-header-bar> <ion-content ng-cloak> <div id="image"> <img ng-src="{{form.photo}}"> <div class="title">{{ form.title }}</div></div> <ul class="list"> <li class="padding"> <button ng-repeat="social in sociais" ng-click="share(form, social)"class="button button-block button-{{ social }}"><i class="icon ion-social-{{ social }}"></i>{{ social | uppercase }} </button> </li> </ul> </ion-content> </ion-modal-view>';

            if (image === undefined) return false;
            var scope   = $rootScope.$new(true);
            var socials = [
                'facebook',
                'instagram',
                'whatsapp',
                'twitter'
            ];
            //image = image.attributes;

            scope.sociais = socials;
            scope.share   = shareSocial;
            scope.form    = {
                title: '',
                photo: image
            };

            scope.modal = $ionicModal.fromTemplate(template, {
                scope: scope
            });
            scope.modal.show();


            function shareSocial(social, form) {
                console.log('share', social, form);
                PhotogramShare
                    .share(social, {
                        text: form.title,
                        image: form.photo,
                        link: AppConfig.app.url
                    });
            }


        }

        function filterModal(image) {
            var defer = $q.defer();

            function openFilter(image) {
                var templateFilter =
                        '<ion-modal-view class="modal-capture"><ion-header-bar class="bar-light"><button class="button button-clear button-icon ion-ios-arrow-thin-left" ng-click="cropImage()"></button><div class="title">{{ \'ION-PHOTO.FILTERS\' | translate }}</div><button class="button button-icon " ng-click="submitFilter()"><i class="icon ion-ios-arrow-thin-right"></i></button></ion-header-bar><ion-content><photo-filter image="form.photo"></photo-filter></ion-content></ion-modal-view>';
                var scope          = $rootScope.$new(true);
                scope.closeFilter  = closeFilter;
                scope.submitFilter = submitFilter;
                tempImage          = image;
                scope.form         = {
                    photo: image
                };

                scope.modalFilter = $ionicModal.fromTemplate(templateFilter, {
                    scope: scope
                });

                scope.cropImage = function () {
                    scope.modalFilter.remove();
                    cropModal(tempImage)
                        .then(openFilter);
                };

                scope.modalFilter.show();

                function closeFilter() {
                    defer.reject();
                    scope.modalFilter.hide();
                }

                function submitFilter() {
                    var canvas  = window.document.getElementById('image');
                    var dataUrl = canvas.toDataURL();
                    scope.modalFilter.remove();
                    defer.resolve(dataUrl);
                }

            }

            openFilter(image);

            return defer.promise;
        }


        function capture(type, options) {
            var defer = $q.defer();

            // CAMERA
            if (type === 0) {
                getPicture(0);
            }

            // GALLERY
            if (type === 1) {
                getPicture(1);
            }

            // Video
            if (type === 2) {
                getVideo();
            }

            function getPicture(method) {
                if (method === 0 && cordova) options.sourceType = Camera.PictureSourceType.CAMERA;
                if (method === 1 && cordova) options.sourceType = Camera.PictureSourceType.PHOTOLIBRARY;

                if (cordova) {
                    $cordovaCamera
                        .getPicture(options)
                        .then(defer.resolve, defer.reject);
                } else {
                    var fileInput = angular.element('<input type="file"/>');
                    fileInput[0].click();
                    fileInput.on('change', function (evt) {
                        tempImage     = evt.currentTarget.files[0];
                        var reader    = new FileReader();
                        reader.onload = function (evt) {
                            defer.resolve(evt.target.result);
                        };
                        reader.readAsDataURL(tempImage);
                    });
                }
            }

            function getVideo() {
                $cordovaCapture
                    .captureVideo({
                        limit: 1,
                        duration: 5
                    })
                    .then(function (mediaFiles) {
                        tempImage = mediaFiles[0].fullPath;
                        defer.resolve(tempImage);
                    }, defer.reject);
            }


            return defer.promise;
        }
    }
})();

(function () {
  'use strict';

  angular
    .module('ion-photo')
    .factory('Video', VideoFactory);

  function VideoFactory($window, $q) {
    var deferred = $q.defer();
    //https://devdactic.com/capture-and-store-videos-ionic/
    // Resolve the URL to the local file
    // Start the copy process
    function createFileEntry(fileURI) {
      $window.resolveLocalFileSystemURL(fileURI, function (entry) {
        return copyFile(entry);
      }, fail);
    }

    // Create a unique name for the videofile
    // Copy the recorded video to the app dir
    function copyFile(fileEntry) {
      var name = fileEntry.fullPath.substr(fileEntry.fullPath.lastIndexOf('/') + 1);
      var newName = makeid() + name;

      $window.resolveLocalFileSystemURL(cordova.file.dataDirectory, function (fileSystem2) {
          fileEntry.copyTo(fileSystem2, newName, function (succ) {
            return onCopySuccess(succ);
          }, fail);
        },
        fail
      );
    }

    // Called on successful copy process
    // Creates a thumbnail from the movie
    // The name is the moviename but with .png instead of .mov
    function onCopySuccess(entry) {
      var name = entry.nativeURL.slice(0, -4);
      $window.PKVideoThumbnail.createThumbnail(entry.nativeURL, name + '.png', function (prevSucc) {
        return prevImageSuccess(prevSucc);
      }, fail);
    }

    // Called on thumbnail creation success
    // Generates the currect URL to the local moviefile
    // Finally resolves the promies and returns the name
    function prevImageSuccess(succ) {
      var correctUrl = succ.slice(0, -4);
      correctUrl += '.MOV';
      deferred.resolve(correctUrl);
    }

    // Called when anything fails
    // Rejects the promise with an Error
    function fail(error) {
      console.log('FAIL: ' + error.code);
      deferred.reject('ERROR');
    }

    // Function to make a unique filename
    function makeid() {
      var text = '';
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      for (var i = 0; i < 5; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      }
      return text;
    }

    // The object and functions returned from the Service
    return {
      // This is the initial function we call from our controller
      // Gets the videoData and calls the first service function
      // with the local URL of the video and returns the promise
      saveVideo: function (data) {
        createFileEntry(data[0].localURL);
        return promise;
      }
    };
  }

})();

(function () {
    'use strict';
    angular
        .module('app.photogram')
        .factory('ConnectMonitor', ConnectMonitorFactory);

    function ConnectMonitorFactory($rootScope, $cordovaNetwork) {
        
        return {
            watch: watch
        };

        function watch() {

            if (ionic.Platform.isWebView()) {
                $rootScope.onLine = $cordovaNetwork.isOnline();
                $rootScope.$apply();


                $rootScope.$on('$cordovaNetwork:online', function (event, networkState) {
                    console.log('went online');
                    $rootScope.onLine = true;
                    $rootScope.$apply();
                });

                $rootScope.$on('$cordovaNetwork:offline', function (event, networkState) {
                    console.log('went offline');
                    $rootScope.onLine = false;
                    $rootScope.$apply();
                })
            } else {
                $rootScope.onLine = navigator.onLine;
                $rootScope.$apply();

                window.addEventListener('online', function (e) {
                    console.log('went online');
                    $rootScope.onLine = true;
                    $rootScope.$apply();
                }, false)

                window.addEventListener('offline', function (e) {
                    console.log('went offline');
                    $rootScope.onLine = false;
                    $rootScope.$apply();
                }, false)
            }
        }

    }

})();
(function () {
    'use strict';
    angular
        .module('app.photogram')
        .factory('dataUtil', dataUtilFactory);

    function dataUtilFactory() {

        return {
            parseData: parseData,
            parseString: parseString,
            getDate: getDate,
            formatDate: formatDate,
            getMonthName: getMonthName,
            getFirstDay: getFirstDay,
            getLastDay: getLastDay
        };

        function parseData(string) {
            var parts = string.split('-');
            return new Date(parts[0], parts[1] - 1, parts[2]);
        }

        function parseString(data) {
            return new Date(data).toLocaleDateString();
        }


        function getDate() {
            var data   = new Date();
            var inicio = "00";
            var year   = data.getFullYear();
            var month  = (inicio + (data.getMonth() + 1)).slice(-inicio.length);
            var day    = (inicio + (data.getDate())).slice(-inicio.length);
            return year + '-' + month + '-' + day;
        }

        function formatDate(milliseconds) {
            var data   = new Date(milliseconds);
            var inicio = "00";
            var year   = data.getFullYear();
            var month  = (inicio + (data.getMonth() + 1)).slice(-inicio.length);
            var day    = (inicio + (data.getDate())).slice(-inicio.length);
            return year + '-' + month + '-' + day;
        }

        function getMonthName(data) {
            var meses = ['Janeiro', 'Feveiro', 'Maro', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'];
            return meses[data.getMonth()];
        }

        function getFirstDay(data) {
            var year  = data.getFullYear();
            var month = data.getMonth();
            return new Date(year, month, 1);
        }

        function getLastDay(data) {
            var year  = data.getFullYear();
            var month = data.getMonth() + 1;
            return new Date(year, month, 0);
        }


    }

})();
(function () {
    'use strict';

    angular
        .module('app.photogram')
        .factory('Photogram', PhotogramFactory);

    function PhotogramFactory($q, Parse, DAO, $rootScope, User, Loading) {

        var limitComment = 3;

        return {
            // Feed
            feed: feed,
            getOfflineFeed: _getOfflineFeed,
            feedGrid: feedGrid,
            post: post,
            get: get,
            deletePhoto: deletePhoto,
            find: find,
            nearby: nearby,
            // Activity
            addActivity: addActivity,
            listActivity: listActivity,
            // User
            getUserGallery: getUserGallery,
            getUserGalleryQtd: getUserGalleryQtd,
            // Comment
            allComment: allComment,
            addComment: addComment,
            getComments: getComments,
            updateComment: updateComment,
            deleteComment: deleteComment,
            // Like
            getLikes: getLikes,
            likeGallery: likeGallery,
            // Follow
            getFollow: getFollow,
            search: search
        };


        function deletePhoto(galleryId) {
            var defer = $q.defer();

            new Parse
                .Query('Gallery')
                .get(galleryId, function (resp) {
                    resp.destroy();
                    defer.resolve(resp);
                });

            return defer.promise;
        }

        function post(_params) {
            var defer       = $q.defer();
            var ImageObject = Parse.Object.extend('Gallery');

            if (_params.photo !== '') {

                // create the parse file
                var imageFile = new Parse.File('mypic.jpg', {
                    base64: _params.photo
                });

                // save the parse file
                imageFile
                    .save()
                    .then(function () {

                        _params.photo = null;

                        // create object to hold caption and file reference
                        var imageObject = new ImageObject();

                        // set object properties
                        imageObject.set('title', _params.title);
                        imageObject.set('user', Parse.User.current());
                        imageObject.set('img', imageFile);

                        if (_params.location !== undefined) {
                            imageObject.set('location', new Parse.GeoPoint(_params.location.latitude, _params.location.longitude));
                        }

                        // save object to parse backend
                        imageObject
                            .save()
                            .then(function (resp) {
                                console.log('Posted Photo', resp);
                                // Add User QtdPhoto
                                defer.resolve(resp);
                            });

                    }, function (error) {
                        console.log('Error', error);
                        defer.reject(error);
                    });

            } else {
                // create object to hold caption and file reference
                var imageObject = new ImageObject();

                // set object properties
                imageObject.set('caption', _params.caption);

                // save object to parse backend
                return imageObject.save();

            }


            return defer.promise;
        }

        function allComment(galleryId) {
            var defer = $q.defer();

            var gallery = new Parse
                .Query('Gallery')
                .get(galleryId, function (resp) {
                    console.log(resp);
                    return resp;
                });

            new Parse
                .Query('GalleryComment')
                .equalTo('galery', gallery)
                //.include ('commentBy')
                .descending('createdAt')
                .find()
                .then(function (resp) {
                    var objs = [];
                    angular.forEach(resp, function (item) {
                        var obj  = item.attributes;
                        obj.id   = item.id;
                        obj.user = item.attributes.commentBy;
                        objs.push(obj);
                    });
                    Loading.end();
                    defer.resolve(objs);
                });
            return defer.promise;
        }

        function getComments(obj) {
            var defer = $q.defer();

            new Parse
                .Query('Gallery')
                .get(obj)
                .then(function (gallery) {
                    new Parse
                        .Query('GalleryComment')
                        .equalTo('gallery', gallery)
                        .include('commentBy')
                        .ascending('createdAt')
                        .find()
                        .then(function (resp) {
                            var comments = [];
                            resp.map(function (item) {
                                console.warn(item);
                                var obj        = {
                                    id: item.id,
                                    text: item.attributes.text,
                                    created: item.createdAt,
                                    user: item.attributes.commentBy
                                };
                                obj.userAvatar = User.avatar(obj.user.attributes)
                                comments.push(obj);
                            });
                            defer.resolve(comments);
                        });
                });

            return defer.promise;
        }

        function updateComment(obj) {
            var defer = $q.defer();
            console.log('updateComment', obj);
            new Parse
                .Query('GalleryComment')
                .get(obj.id, function (comment) {
                    console.log('updateComment comment', comment);
                    comment.set('text', obj.text);
                    comment.save();
                    defer.resolve();
                });
            return defer.promise;
        }

        function deleteComment(item) {
            var defer = $q.defer();
            new Parse
                .Query('GalleryComment')
                .get(item.id, deleteItem);

            function deleteItem(comment) {
                comment.destroy(function () {
                    defer.resolve();
                });
            }

            return defer.promise;
        }

        function getFollow(userId) {
            var defer = $q.defer();

            User
                .find(userId)
                .then(function (user) {

                    new Parse
                        .Query('UserFollow')
                        .equalTo('user', user)
                        .include('follow')
                        .find()
                        .then(function (resp) {
                            var data = [];
                            angular.forEach(resp, function (item) {
                                console.warn(item);
                                var obj        = {
                                    id: item.id,
                                    text: item.attributes.text,
                                    user: item.attributes.follow.attributes,
                                    created: item.createdAt
                                };
                                obj.userAvatar = User.avatar(obj.user.attributes);

                                data.push(obj.user);
                            });
                            defer.resolve(data);
                        });
                });

            return defer.promise;
        }

        function getLikes(obj) {
            var defer = $q.defer();

            new Parse
                .Query('Gallery')
                .get(obj)
                .then(function (gallery) {
                    if (gallery.length) {
                        new Parse
                            .Query('GalleryLike')
                            .equalTo('gallery', gallery)
                            .include('user')
                            .ascending('createdAt')
                            .find()
                            .then(function (resp) {
                                var objs = [];
                                angular.forEach(resp, function (item) {
                                    console.warn(item);
                                    var obj        = {
                                        id: item.id,
                                        text: item.attributes.text,
                                        user: item.attributes.user.attributes,
                                        created: item.createdAt
                                    };
                                    obj.userAvatar = User.avatar(obj.user.attributes);
                                    objs.push(obj);
                                });
                                console.log(objs);
                                defer.resolve(objs);
                            });
                    } else {
                        defer.reject(true);
                    }
                });

            return defer.promise;
        }


        function nearby(position) {
            var defer = $q.defer();
            var data  = [];

            var point       = new Parse.GeoPoint(position);
            var maxDistance = 10;

            new Parse
                .Query('Gallery')
                .near('location', point)
                .withinRadians('location', point, maxDistance)
                .include('user')
                // .limit(10)
                .find()
                .then(function (resp) {
                    if (resp.length) {
                        resp.map(function (value) {
                            var size     = 100;
                            var obj      = angular.copy(value.attributes);
                            obj.id       = value.id;
                            obj.img      = User.avatar(value.attributes);
                            obj.created  = value.createdAt;
                            obj.icon     = {
                                size: {
                                    width: 100,
                                    height: 100
                                },

                                scaledSize: {
                                    width: size / 2,
                                    height: size / 2
                                },
                                url: 'img/icon.png'
                            };
                            obj.icon.url = obj.img;
                            obj.coords   = {
                                latitude: obj.location._latitude,
                                longitude: obj.location._longitude
                            };
                            data.push(obj);
                        });


                        defer.resolve(data);
                    } else {
                        defer.reject(true);
                    }
                });

            return defer.promise;
        }

        function search(string, page) {
            var defer = $q.defer();
            var data  = [];
            var limit = 15;

            new Parse
                .Query('Gallery')
                .limit(limit)
                .skip(page * limit)
                .matches('title', '* ' + string + '.*')
                .include('user')
                .find()
                .then(function (resp) {
                    resp.map(function (value) {
                        console.log('gallery search item', value);
                        var obj = {
                            id: value.id,
                            item: value.attributes,
                            src: value.attributes.img.url(),
                            created: value.createdAt,
                            user: value.attributes.user.id
                        };
                        data.push(obj);
                    });
                    defer.resolve(data);
                });

            return defer.promise;
        }


        function _getOfflineFeed(page, user, _limit) {
            var defer = $q.defer();

            DAO.paginate('Gallery',
                'User.name as userName, User.status as userStatus, User.img as userAvatar, Gallery.*',
                '',
                'INNER JOIN User ON Gallery.user_id = User.id',
                'ORDER BY Gallery.createdAt DESC',
                _limit,
                page).then(function (data) {
                console.log(data);
                defer.resolve(data);
            });

            return defer.promise;
        }

        function _getOnlineFeed(page, user, _limit) {
            var defer   = $q.defer();
            var _result = {
                total: 0,
                rows: []
            };

            function _query() {
                if (user) {
                    return new Parse
                        .Query('Gallery')
                        .descending('createdAt')
                        .equalTo('user', user);
                } else {
                    return new Parse
                        .Query('Gallery')
                        .descending('createdAt');
                    //.notEqualTo('user', user)
                    //.containedIn('ref', following)
                    //.containsAll('ref', following)
                }
            };


            _query()
                .count()
                .then(function (totalGallery) {
                    console.log('results', totalGallery);
                    _result.total = totalGallery;
                    _query()
                        .include('user')
                        .limit(_limit)
                        .skip(page * _limit)
                        .find()
                        .then(function (resp) {

                            // console.log('home', resp);
                            var qtd = resp.length;

                            if (!qtd) {
                                defer.reject(true);
                            }

                            var cb = _.after(resp.length, function () {
                                defer.resolve(_result);
                            });

                            _.each(resp, function (item) {
                                //grab relations

                                var _likes    = item.relation('likes');
                                var _comments = item.relation('comments');

                                _likes
                                    .query()
                                    .equalTo('gallery', item)
                                    .equalTo('user', Parse.User.current())
                                    .count()
                                    .then(function (liked) {
                                        (liked > 0) ? liked = true : liked = false;
                                        _comments
                                            .query()
                                            .include('commentBy')
                                            .ascending('createdAt')
                                            .limit(limitComment)
                                            .find()
                                            .then(function (comments) {

                                                var commentsData = [];
                                                comments.map(function (item) {
                                                    var user = item.attributes.commentBy;

                                                    var comment = {
                                                        id: item.id,
                                                        text: item.attributes.text,
                                                        user: user,
                                                        created: item.attributes.createdAt,
                                                        userAvatar: User.avatar(user.attributes),
                                                        userName: user.attributes.name
                                                    };
                                                    User.cacheUser(comment.user);

                                                    var _queryInsertComment = {
                                                        table: 'GalleryComment',
                                                        columns: {
                                                            id: comment.id,
                                                            name: comment.text,
                                                            user_id: comment.user.id,
                                                            userName: comment.user.attributes.name,
                                                            gallery_id: item.id,
                                                            createdAt: comment.createdAt ? new Date(comment.createdAt).getTime() : new Date().getTime()
                                                        }
                                                    };
                                                    DAO.insert(_queryInsertComment);

                                                    commentsData.push(comment);
                                                });

                                                var obj                 = {
                                                    id: item.id,
                                                    item: angular.copy(item.attributes),
                                                    created: item.createdAt,
                                                    likes: item.attributes.qtdLike || 0,
                                                    liked: liked,
                                                    img: item.attributes.img._url,
                                                    comments: commentsData,
                                                    userName: item.attributes.user.attributes.name,
                                                    userAvatar: User.avatar(item.attributes.user.attributes),
                                                    userStatus: item.attributes.user.attributes.status
                                                };
                                                var _queryInsertGallery = {
                                                    table: 'Gallery',
                                                    columns: {
                                                        id: item.id,
                                                        title: item.attributes.title,
                                                        img: item.attributes.img._url,
                                                        user_id: item.attributes.user.id,
                                                        location: JSON.stringify(item.attributes.location),
                                                        qtdLike: item.attributes.qtdLike || 0,
                                                        liked: liked,
                                                        likes: item.attributes.qtdLike || 0,
                                                        createdAt: new Date(item.createdAt).getTime()
                                                    }
                                                };
                                                DAO.insert(_queryInsertGallery);
                                                // console.table(obj);
                                                _result.rows.push(obj);
                                                cb();
                                            });
                                    });

                            });
                        });
                });
            return defer.promise;
        }

        function feed(page, user) {
            var _limit = 15;
            // Offline Feed

            return _getOfflineFeed(page, user, _limit);

            if ($rootScope.onLine) {
                return _getOnlineFeed(page, user, _limit);
            } else {
                return _getOfflineFeed(page, user, _limit);
            }
        }

        function feedGrid(page, user, options) {
            var defer   = $q.defer();
            var _limit  = 30;
            var _result = {
                total: 0,
                rows: []
            };

            function _query() {
                if (user) {
                    return new Parse
                        .Query('Gallery')
                        .descending('createdAt')
                        .equalTo('user', user);
                } else {
                    return new Parse
                        .Query('Gallery')
                        .descending('createdAt');
                }
            };

            _query()
                .count()
                .then(function (totalGallery) {
                    console.log('results', totalGallery);
                    _result.total = totalGallery;
                    _query()
                        .include('user')
                        .limit(_limit)
                        .skip(page * _limit)
                        .find()
                        .then(function (resp) {

                            var qtd = resp.length;

                            if (!qtd) {
                                defer.reject(true);
                            }

                            var cb = _.after(resp.length, function () {
                                defer.resolve(_result);
                            });

                            _.each(resp, function (item) {
                                var obj = {
                                    id: item.id,
                                    item: angular.copy(item.attributes),
                                    created: item.createdAt,
                                    likes: item.attributes.qtdLike || 0,
                                    img: item.attributes.img.url(),
                                    user: item.attributes.user,
                                    userAvatar: User.avatar(item.attributes.user.attributes)
                                };
                                _result.rows.push(obj);
                                cb();
                            });
                        });
                });
            return defer.promise;
        }


        function get(item) {
            var defer = $q.defer();

            console.log(item);
            Loading.start();

            find(item)
                .then(function (resp) {
                    console.log(resp);

                    var likes    = resp.relation('likes');
                    var comments = resp.relation('comments');

                    likes
                        .query()
                        .equalTo('gallery', item)
                        .equalTo('user', Parse.User.current())
                        .count()
                        .then(function (liked) {

                            likes
                                .query()
                                .count()
                                .then(function (likes) {

                                    comments
                                        .query()
                                        .include('commentBy')
                                        .descending('createdAt')
                                        .limit(limitComment)
                                        .find()
                                        .then(function (comments) {
                                            console.log(comments);

                                            var commentsData = [];

                                            angular.forEach(comments, function (item) {
                                                var comment        = {
                                                    id: item.id,
                                                    text: item.attributes.text,
                                                    user: item.attributes.commentBy.attributes
                                                };
                                                comment.userAvatar = User.avatar(comment.user);
                                                commentsData.push(comment);
                                            });

                                            var obj        = {
                                                id: item.id,
                                                item: item.attributes,
                                                created: item.createdAt,
                                                likes: likes,
                                                liked: liked,
                                                user: item.attributes.user.attributes,
                                                comments: commentsData
                                            };
                                            obj.userAvatar = User.avatar(obj.user);

                                            defer.resolve(obj);
                                            Loading.end();
                                        });
                                });
                        });

                });

            return defer.promise;
        }


        function find(id) {
            var defer = $q.defer();
            new Parse
                .Query('Gallery')
                .include('user')
                .get(id)
                .then(function (resp) {
                    defer.resolve(resp);
                });
            return defer.promise;
        }


        function addComment(form) {
            var defer = $q.defer();
            console.log('addComent', form);

            find(form.galleryId)
                .then(function (gallery) {
                    var Object = Parse.Object.extend('GalleryComment');
                    var item   = new Object({});

                    angular.forEach(form, function (value, key) {
                        item.set(key, value);
                    });
                    item.set('commentBy', Parse.User.current());
                    item.set('gallery', gallery);

                    item.save(null)
                        .then(function (resp) {
                            console.log(resp);

                            addActivity({
                                galleryId: gallery.id,
                                action: 'add comment'
                            });

                            gallery
                                .relation('comments')
                                .add(resp);

                            gallery
                                .save()
                                .then(function (resp) {
                                    console.log(resp);
                                    defer.resolve(resp);
                                });
                        });
                });


            return defer.promise;
        }

        function isLiked(galleryId) {
            var defer = $q.defer();

            find(galleryId)
                .then(function (gallery) {
                    new Parse
                        .Query('GalleryLike')
                        .equalTo('gallery', gallery)
                        .equalTo('user', Parse.User.current())
                        .first()
                        .then(function (resp) {
                            console.warn(resp);
                            if (resp === undefined) {
                                defer.reject(resp);
                            } else {
                                defer.resolve(resp);
                            }
                        });
                });

            return defer.promise;
        }

        function addLike(galleryId) {
            var defer = $q.defer();

            find(galleryId)
                .then(function (gallery) {
                    var Object = new Parse.Object.extend('GalleryLike');
                    var item   = new Object({});

                    item.set('user', Parse.User.current());
                    item.set('gallery', gallery);

                    console.log(gallery);

                    item.save(null)
                        .then(function (resp) {
                            console.log(resp);

                            // Gallery Increment Like
                            var likes = parseInt(gallery.attributes.qtdLike) ? parseInt(gallery.attributes.qtdLike + 1) : 1;
                            console.log('Qtd Like', likes);

                            // Increment Like
                            gallery.set('qtdLike', likes);

                            // Add Relation
                            gallery.relation('likes').add(resp);

                            console.log('Save Gallery', gallery);

                            // Save Gallery
                            gallery
                                .save()
                                .then(function (newGallery) {
                                    console.log(newGallery);
                                    defer.resolve({
                                        liked: true,
                                        likes: likes
                                    });
                                }, function (err) {
                                    console.error(err);
                                    defer.reject(err);
                                });
                        });
                });
            return defer.promise;
        }

        function removeLike(galleryId) {

            var defer = $q.defer();
            find(galleryId)
                .then(function (gallery) {

                    new Parse
                        .Query('GalleryLike')
                        .equalTo('gallery', gallery)
                        .equalTo('user', Parse.User.current())
                        .first()
                        .then(function (like) {

                            var likes = parseInt(gallery.attributes.qtdLike - 1);
                            if (likes < 0) {
                                likes = 0;
                            }

                            console.log('Remove like', likes, gallery);


                            // Gallery Decrement Like
                            gallery.set('qtdLike', likes);

                            // Remove Relation
                            gallery.relation('likes').remove(like);

                            like
                                .destroy(function (resp) {
                                    if (resp) {
                                        console.log('Remove like');
                                        // Save Gallery
                                        gallery
                                            .save()
                                            .then(function (newGallery) {
                                                console.log('New Gallery', newGallery);

                                                defer.resolve({
                                                    liked: false,
                                                    likes: newGallery.attributes.qtdLike
                                                });

                                            }, function (err) {
                                                console.error(err);
                                                defer.reject(err);
                                            });
                                    }
                                });


                        });

                });
            return defer.promise;
        }

        function likeGallery(gallery) {
            var defer = $q.defer();

            isLiked(gallery)
                .then(function (resp) {
                    console.warn(resp);
                    var promise = '';

                    if (resp) {
                        console.log('Remove Like');
                        promise = removeLike(gallery);
                        addActivity({
                            galleryId: gallery,
                            action: 'unlike like'
                        });

                    } else {
                        console.log('Add like');
                        promise = addLike(gallery);
                        addActivity({
                            galleryId: gallery,
                            action: 'add like'
                        });
                    }

                    promise
                        .then(function (resp) {
                            console.log(resp);
                            defer.resolve(resp);
                        });

                })
                .catch(function (err) {
                    console.log('Add like', err);

                    addActivity({
                        galleryId: gallery,
                        action: 'add like'
                    });

                    addLike(gallery)
                        .then(function (resp) {
                            console.log(resp);
                            defer.resolve(resp);
                        });
                });
            return defer.promise;
        }

        function getUserGalleryQtd(userId) {
            var defer = $q.defer();
            if (userId === undefined) {
                userId = Parse.User.current().id;
            }

            User
                .find(userId)
                .then(function (user) {
                    new Parse
                        .Query('Gallery')
                        .equalTo('user', user)
                        .count()
                        .then(function (qtdGalleries) {
                            defer.resolve(qtdGalleries);
                        });
                });

            return defer.promise;
        }

        function getUserGallery(userId, page) {
            var defer = $q.defer();
            var data  = [];
            var limit = 9;

            if (userId === undefined) {
                userId = Parse.User.current().id;
            }

            User
                .find(userId)
                .then(function (user) {

                    new Parse
                        .Query('Gallery')
                        .equalTo('user', user)
                        .descending('createdAt')
                        //.containedIn('ref', following)
                        //.containsAll('ref', following)
                        .include('user')
                        .limit(limit)
                        .skip(page * limit)
                        .find()
                        .then(function (resp) {

                            if (resp.length) {
                                var cb = _.after(resp.length, function () {
                                    defer.resolve(data);
                                });

                                _.each(resp, function (item) {
                                    //grab relations

                                    var likes    = item.relation('likes');
                                    var comments = item.relation('comments');

                                    likes
                                        .query()
                                        .equalTo('gallery', item)
                                        .equalTo('user', Parse.User.current())
                                        .count()
                                        .then(function (liked) {

                                            likes
                                                .query()
                                                .count()
                                                .then(function (likes) {

                                                    comments
                                                        .query()
                                                        .include('commentBy')
                                                        .descending('createdAt')
                                                        .limit(limitComment)
                                                        .find()
                                                        .then(function (comments) {
                                                            // console.log(comments);
                                                            var commentsData = [];

                                                            angular.forEach(comments, function (item) {
                                                                var comment        = {
                                                                    id: item.id,
                                                                    text: item.attributes.text,
                                                                    user: item.attributes.commentBy.attributes
                                                                };
                                                                comment.userAvatar = User.avatar(comment.user);
                                                                commentsData.push(comment);
                                                            });

                                                            var obj        = {
                                                                id: item.id,
                                                                item: item.attributes,
                                                                src: item.attributes.img.url(),
                                                                created: item.createdAt,
                                                                likes: likes,
                                                                liked: liked,
                                                                comments: commentsData,
                                                                user: item.attributes.user.attributes
                                                            };
                                                            // obj.user.id = item.attributes.user.id;
                                                            obj.userAvatar = User.avatar(obj.user);

                                                            data.push(obj);
                                                            cb();
                                                        });
                                                });
                                        });

                                });
                            } else {
                                defer.reject(true);
                            }
                        }, function () {
                            defer.reject(true);
                        });
                });

            return defer.promise;
        }

        function listActivity(page) {
            var defer  = $q.defer();
            var limit  = 20;
            var _table = 'GalleryActivity';

            console.info(page, limit);


            DAO.query("SELECT * FROM " + _table).then(function (data) {
                if (data.rows.length) {
                    defer.resolve(data.rows);
                } else {
                    new Parse
                        .Query('GalleryActivity')
                        .include('user')
                        .include('gallery')
                        .descending('createdAt')
                        .limit(limit)
                        .skip(page * limit)
                        .find()
                        .then(function (resp) {
                            if (resp.length) {
                                var data = [];
                                resp.map(function (item) {
                                    var _insert = {
                                        table: _table,
                                        columns: {
                                            id: item.id,
                                            action: item.attributes.action,
                                            user_id: item.attributes.user.id,
                                            userName: item.attributes.user.attributes.name,
                                            userAvatar: User.avatar(item.attributes.user.attributes),
                                            gallery_id: item.attributes.gallery ? item.attributes.gallery.id : null,
                                            img: item.attributes.gallery ? item.attributes.gallery.attributes.img._url : null,
                                            createdAt: new Date(item.attributes.createdAt).getTime()
                                        }
                                    }
                                    DAO.insert(_insert).then(console.log(data));
                                    data.push(_insert.columns);
                                });
                                defer.resolve(data);
                            } else {
                                defer.reject(true);
                            }
                        });
                }
            })


            return defer.promise;
        }

        function addActivity(data) {
            /*
             * ACTIONS
             * add photo
             * add comment
             * like photo
             * unlike photo
             * register
             * */

            console.info(data);

            if (data.galleryId) {
                find(data.galleryId)
                    .then(function (gallery) {
                        var Object = Parse.Object.extend('GalleryActivity');
                        var item   = new Object({});

                        item.set('user', Parse.User.current());
                        item.set('gallery', gallery);
                        item.set('action', data.action);

                        item.save()
                            .then(function (resp) {
                                console.warn(resp);
                            });
                    });
            } else {
                var Object = Parse.Object.extend('GalleryActivity');
                var item   = new Object({});

                item.set('user', Parse.User.current());
                item.set('action', data.action);

                item.save()
                    .then(function (resp) {
                        console.warn(resp);
                    });
            }
        }


    }


})();
