(function () {
    'use strict';
    angular
        .module('app.photogram')
        .factory('PhotogramSetting', PhotogramSettingFactory);

    function PhotogramSettingFactory(DAO, $q) {

        var _data = {
            rows:[]
        };

        return {
            init: init,
            get: get
        };

        function init() {
            var defer = $q.defer();
            var _tableName = 'GallerySetting';

            DAO.query("SELECT * FROM " + _tableName).then(function  (data) {
                    console.log(data);
                    if(data.rows.length) {
                        _data.rows = data.rows;
                        defer.resolve(_data);
                    } else {

                        new Parse
                            .Query(_tableName)
                            .find()
                            .then(function (resp) {
                                var data  = [];
                                resp.map(function (item) {
                                    var _queryInsert = {
                                        table: _tableName,
                                        columns: {
                                            key: item.attributes.key,
                                            value: item.attributes.value
                                        }
                                    };
                                    DAO.insert(_queryInsert);
                                    data.push(_queryInsert.columns)
                                });
                                defer.resolve(data);
                            }, error);
                        
                    }
                })


            return defer.promise;

        }

        function error(err) {
            alert(err);
        }

        function get(key) {
            return _.filter(_data.rows,{key: key})[0];
        }

    }


})();
(function () {
  'use strict';
  angular
    .module('app.photogram')
    .factory('PhotogramShare', PhotogramShareFactory);

  function PhotogramShareFactory(AppConfig, $q, $ionicActionSheet, Notify, $cordovaSocialSharing) {

    var message = {
      title: ('Join me from ') + AppConfig.app.name + '!',
      subject: ("I'm at ") + AppConfig.app.name + '!. ' + (
        'Install the application and follow me!') + ' ' + AppConfig.app.url,
      image: AppConfig.app.image,
      link: AppConfig.app.url
    };

    return {
      share: share,
      open: open
    };

    function share(social, option) {
      var defer = $q.defer();

      function success(resp) {
        Notify.alert({
          title: ('Thanks'),
          text: ('Thank you for sharing!!')
        });
        defer.resolve(resp);
      }

      function error(err) {
        console.error(err);
        defer.reject();
      }

      var detail = option ? option : message;

      switch (social) {
      case 'instagram':
        window
          .plugins
          .socialsharing
          .shareViaInstagram(message.text, message.image, message.link)
          .then(success, error);
        break;

      case 'facebook':
        $cordovaSocialSharing
          .shareViaFacebook(detail.text, detail.image, detail.link)
          .then(success, error);
        break;

      case 'twitter':
        $cordovaSocialSharing
          .shareViaTwitter(detail.text, detail.image, detail.link)
          .then(success, error);
        break;

      case 'whatsapp':
        $cordovaSocialSharing
          .shareViaWhatsApp(detail.text, detail.image, detail.link)
          .then(success, error);
        break;

      case 'email':
        $cordovaSocialSharing
          .shareViaEmail(detail.title, detail.subject ? detail.subject : detail.title)
          .then(success, error);
        break;
      }

      return defer.promise;
    }

    function open() {
      var modal = $ionicActionSheet
        .show({
          buttons: [{
            text: '<i class="icon ion-social-instagram"></i>' + ('Instagram')
          }, {
            text: '<i class="icon ion-social-facebook"></i>' + ('Facebook')
          }, {
            text: '<i class="icon ion-social-twitter"></i>' + ('Twitter')
          }, {
            text: '<i class="icon ion-social-whatsapp"></i>' + ('Whatsapp')
          }, {
            text: '<i class="icon ion-email"></i>' + ('Email')
          }],
          titleText: ('Share'),
          cancelText: ('Cancel'),
          cancel: function () {
            return false;
          },
          buttonClicked: function (index) {
            console.log(index);
            switch (index) {
            case 0:
              share('instagram');
              break;
            case 1:
              share('facebook');
              break;
            case 2:
              share('twitter');
              break;
            case 3:
              share('whatsapp');
              break;
            case 4:
              share('email');
              break;
            }
            modal();
            //share(index);
          }
        });

    }
  }

})();
(function () {
    'use strict';
  
    angular
        .module('app.user')
        .factory('User', UserFactory);

    function UserFactory($q, $window, AppConfig, Parse, $rootScope, $ionicHistory, $cordovaDevice, $facebook, $cordovaFacebook, Loading, DAO, $state) {

        var cordova  = $window.cordova;
        var device   = cordova ? true : false;
        var facebook = device ? $cordovaFacebook : $facebook;
        var user;

        return {
            init: init,
            initFollows: initFollows,
            addFollows: addFollows,
            currentUser: currentUser,
            register: register,
            cacheUser: cacheUser,
            login: login,
            profile: profile,
            logout: logout,
            update: update,
            updateAvatar: updateAvatar,
            forgot: forgot,
            list: list,
            find: find,
            follow: follow,
            avatar: avatar,
            getFollowers: getFollowers,
            getFollowing: getFollowing,
            isFollow: isFollow,
            mail: getMail,
            facebookLogin: facebookLogin,
            facebookLink: facebookLink,
            facebookProfile: facebookProfile,
            facebookFriends: facebookFriends,
            facebookInvite: facebookInvite,
            facebookAPI: facebookAPI
        };


        function init() {
            var defer = $q.defer();
            // Parse Start
            if (Parse.User.current()) {
                user = Parse.User.current();
                loadProfile();
                defer.resolve(user);
            } else {
                console.log('Not logged user, go intro');
                // logout();
                // $location.path(AppConfig.routes.login);
                defer.reject('Not user');
            }
            return defer.promise;
        }

        function initFollows() {
            var defer = $q.defer();
            console.log('Init Follows', Parse.User.current().id);
            DAO.query("SELECT * FROM UserFollow WHERE userId = '" + Parse.User.current().id + "'")
               .then(function (data) {
                   console.log(data);

                   if (data.rows.length) {
                       defer.resolve(data.rows);
                   } else {
                       new Parse
                           .Query('UserFollow')
                           .include('follow')
                           .equalTo('user', Parse.User.current())
                           .find()
                           .then(function (follows) {
                               var _promises = [];
                               console.warn(follows);
                               follows.map(function (item) {

                                   var _query            = {
                                       table: 'UserFollow',
                                       columns: {
                                           id: item.id,
                                           userId: item.attributes.user.id,
                                           followId: item.attributes.follow.id
                                       }
                                   };

                                   _promises.push(cacheUser(item.attributes.follow));
                                   _promises.push(DAO.insert(_query));
                               });

                               $q.all(_promises).then(function (result) {
                                   defer.resolve(result);
                               }).catch(defer.reject);

                           }, function (err) {
                               console.log('nenhum follow', err);
                               defer.resolve([]);
                           });
                   }
               })
               .catch(function (erro) {
                   console.error(erro);
               });
            return defer.promise;
        }


        function currentUser() {
            return user;
        }


        function loadProfile() {
            user = Parse.User.current();
            if (user) {
                // user = avatar(user);
                $rootScope.currentUser = user.attributes;
                console.log('load profile', user);
                return user;
            } else {
                logout();
                return false;
            }
        }

        function avatar(obj) {
            if (obj.facebookimg) {
                return obj.facebookimg;
            } else {
                return obj.img ? obj.img._url : 'img/user.png';
            }
        }

        function login(form) {
            var defer = $q.defer();
            Parse
                .User
                .logIn(form.email, form.password, {
                    success: function (resp) {
                        console.log(resp);
                        loadProfile()
                        defer.resolve($rootScope.currentUser);
                    },
                    error: function (user, err) {
                        console.error(user, err);
                        // The login failed. Check error to see why.
                        defer.reject(err);
                    }
                });
            return defer.promise;
        }

        function loginFacebook2(response) {
            var defer = $q.defer();
            console.log('Facebook api');
            facebook
                .api('me/?fields=id,name,email,gender,bio', ['public_profile'])
                .then(function (dados) {
                    console.log('facebook api', dados);

                    var query = new Parse.Query(Parse.User);
                    query
                        .equalTo('email', dados.email)
                        .first({
                            success: function (user) {
                                console.log(user);

                                if (user) {
                                    console.log('J existe um cadastro com esse email', user);
                                    if (user.get('facebook_complete') === Boolean(true)) {

                                        loginFacebook(response)
                                            .then(function (resp) {
                                                console.log('Logado', resp);

                                                if (user.attributes.name === '') {
                                                    console.info('User sem nome', user, dados);
                                                    var updateUser  = user.attributes;
                                                    updateUser.name = dados.name;

                                                    update(updateUser)
                                                        .then(function () {
                                                            defer.resolve({
                                                                status: 0
                                                            });
                                                        });
                                                } else {

                                                    loadProfile(user);

                                                    defer.resolve({
                                                        status: 0
                                                    });
                                                }

                                            });
                                    } else {
                                        console.log('Se ainda no est completo, manda completar o perfil', dados,
                                            response, user);


                                        $rootScope.tempUser             = dados;
                                        $rootScope.tempUser.facebookimg = 'https://graph.facebook.com/' + dados.id +
                                            '/picture?width=250&height=250';

                                        console.log($rootScope.tempUser);
                                        defer.resolve({
                                            status: 2
                                        });

                                    }

                                } else {
                                    // Se no encontrar nenhum usurio
                                    console.log('Novo usurio');

                                    // Crio uma conta no parse com o Facebook
                                    loginFacebook(response)
                                        .then(function (newuser) {

                                            console.log(newuser);

                                            // Atualizo o novo perfil
                                            var form = {
                                                name: dados.name,
                                                facebook: dados.id,
                                                email: dados.email,
                                                gender: dados.gender,
                                                facebook_complete: Boolean(true),
                                                facebookimg: 'https://graph.facebook.com/' + dados.id +
                                                '/picture?width=250&height=250'
                                            };

                                            update(form)
                                                .then(function (resp) {
                                                    console.warn('me response', resp);

                                                    defer.resolve({
                                                        status: 1
                                                    });
                                                });


                                        });


                                }

                            },
                            error: function (error) {
                                alert('Sem conexo');
                                defer.reject(error);

                            }
                        });

                }, function (resp) {
                    console.log('Facebook Error', resp);
                    defer.reject(resp);
                });

            return defer.promise;
        }

        function facebookLogin() {
            var defer = $q.defer();

            //facebook.logout();
            console.info('facebook login', device, facebook);

            facebook
                .getLoginStatus()
                .then(function (respStatus) {

                    if (respStatus.status === 'connected') {
                        loginFacebook2(respStatus)
                            .then(function (deferStatus) {
                                defer.resolve(deferStatus);
                            });
                    } else {

                        facebook
                            .login([
                                'public_profile',
                                'email'
                            ])
                            .then(function (response) {

                                    console.warn('facebook login response', response);
                                    if (response.status === undefined) {
                                        defer.reject('reject');
                                    }

                                    //Pega o Status do Login
                                    console.log('facebook status', response);
                                    loginFacebook2(response)
                                        .then(function (deferStatus) {
                                            defer.resolve(deferStatus);
                                        });
                                    ;
                                },
                                function (response) {
                                    //alert(JSON.stringify(response));
                                    console.log('Facebook Error', response);
                                    defer.reject(JSON.stringify(response));

                                });
                    }
                    ;
                });


            return defer.promise;
        }


        function facebookProfile() {
            var defer = $q.defer();
            facebookLogin()
                .then(function (resp) {

                    facebook
                        .api('me', '')
                        .then(function (response) {
                            defer.resolve([
                                resp,
                                response
                            ]);
                        }, function (error) {
                            console.log(error);
                            defer.reject(error);
                        });
                });
            return defer.promise;
        }


        function register(form) {
            var defer = $q.defer();

            var formData      = form;
            formData.username = form.email;

            console.log(formData);
            new Parse
                .User(formData)
                .signUp(null, {
                    success: function (resp) {
                        var user = loadProfile(resp);
                        console.log(resp, user);
                        Loading.end();
                        //startPush('user-', user.email);
                        defer.resolve(user);
                    },
                    error: function (user, resp) {
                        console.log(resp);
                        if (resp.code === 125) {
                            defer.reject('Please specify a valid email address');
                        } else if (resp.code === 202) {
                            defer.reject('The email address is already registered');
                        } else {
                            defer.reject(resp);
                        }
                    }
                });
            return defer.promise;
        }

        function forgot(email) {
            var defer = $q.defer();
            new Parse.User.requestPasswordReset(email, {
                success: function (resp) {
                    defer.resolve(resp);
                },
                error: function (err) {
                    if (err.code === 125) {
                        defer.reject('Email address does not exist');
                    } else {
                        defer.reject('An unknown error has occurred, please try again');
                    }
                }
            });
            return defer.promise;
        }

        function logout() {
            new Parse.User.logOut();
            delete $rootScope.currentUser;
            //$window.location = '/#/intro';
            $state.go(AppConfig.routes.login, {
                clear: true
            });
            $ionicHistory.clearCache();
        }

        function update(form) {
            var defer = $q.defer();
            Loading.start();
            var currentUser = Parse.User.current();

            angular.forEach(form, function (value, key) {
                console.log(key, value);
                currentUser.set(key, value);
            });

            if (cordova) {
                var cordovaDevice = {
                    device: $cordovaDevice.getDevice(),
                    cordova: $cordovaDevice.getCordova(),
                    model: $cordovaDevice.getModel(),
                    platform: $cordovaDevice.getPlatform(),
                    uuid: $cordovaDevice.getUUID(),
                    version: $cordovaDevice.getVersion()
                };

                currentUser.set('device', cordovaDevice.device);
                currentUser.set('deviceCordova', cordovaDevice.cordova);
                currentUser.set('deviceModel', cordovaDevice.model);
                currentUser.set('devicePlatform', cordovaDevice.platform);
                currentUser.set('deviceUuiid', cordovaDevice.uuid);
                currentUser.set('deviceVersion', cordovaDevice.version);
            }
            // Update Language
            //currentUser.set('language', $rootScope.lang.value || null);

            // console.log(currentUser);
            currentUser
                .save()
                .then(function (resp) {
                    console.log(resp);
                    loadProfile();
                    Loading.end();
                    defer.resolve(resp);
                });


            return defer.promise;
        }

        function updateAvatar(photo) {
            var defer = $q.defer();

            Loading.start();

            if (photo !== '') {

                // create the parse file
                var imageFile = new Parse.File('mypic.jpg', {
                    base64: photo
                });

                // save the parse file
                return imageFile
                    .save()
                    .then(function () {

                        photo = null;

                        // create object to hold caption and file reference
                        var currentUser = Parse.User.current();

                        // set object properties
                        currentUser.set('img', imageFile);

                        // save object to parse backend
                        currentUser
                            .save()
                            .then(function (resp) {
                                loadProfile();
                                Loading.end();
                                defer.resolve(resp);
                            });


                    }, function (error) {
                        Loading.end();
                        console.log(error);
                        defer.reject(error);
                    });
            }
            return defer.promise;
        }


        function facebookFriends() {
            var defer = $q.defer();

            facebook
                .api('me/friends')
                .then(function (success) {
                    defer.resolve(success);
                }, function (error) {
                    defer.reject(error);
                });

            return defer.promise;
        }

        function facebookAPI(api) {
            var defer = $q.defer();

            facebook
                .api(api)
                .then(function (success) {
                    defer.resolve(success);
                }, function (error) {
                    defer.reject(error);
                });

            return defer.promise;
        }

        function facebookInvite() {
            var defer = $q.defer();
            if (device) {
                facebook
                    .showDialog({
                        method: 'apprequests',
                        message: 'Venha para o nosso clube!'
                    })
                    .then(function (resp) {
                        defer.resolve(resp);
                    });
            } else {
                facebook
                    .ui({
                        method: 'apprequests',
                        message: 'Venha para o nosso clube!'
                    })
                    .then(function (resp) {
                        defer.resolve(resp);
                    });
            }
            return defer.promise;
        }


        function list() {
            var defer = $q.defer();

            new Parse
                .Query('User')
                .notEqualTo('user', Parse.User.current())
                .find()
                .then(function (resp) {
                    var users = [];
                    angular.forEach(resp, function (item) {
                        var user = item.attributes;
                        user.id  = item.id;
                        user     = avatar(user);

                        new Parse
                            .Query('UserFollow')
                            .equalTo('user', Parse.User.current())
                            .equalTo('follow', item)
                            .count()
                            .then(function (follow) {
                                user.follow = follow;

                                console.log(user);
                                users.push(user);
                            });
                    });
                    defer.resolve(users);
                });

            return defer.promise;
        }

        function cacheUser(user) {
            var defer = $q.defer();
            // console.warn('_queryInsert', user);
            var _queryInsert = {
                table: 'User',
                columns: {
                    id: user.id,
                    name: user.attributes.name,
                    username: user.attributes.username,
                    email: user.attributes.email,
                    language: user.attributes.language,
                    facebookimg: user.attributes.facebookimg,
                    gender: user.attributes.gender,
                    img: avatar(user.attributes),
                    facebook: user.attributes.facebook,
                    qtdFollow: user.attributes.qtdFollow,
                    status: user.attributes.status,
                    createdAt: user.attributes.createdAt ? new Date(user.attributes.createdAt).getTime() : 0,
                    attributes: JSON.stringify(user.attributes)
                }
            }

            // console.info(_queryInsert);
            DAO
                .insert(_queryInsert)
                .then(function () {
                    defer.resolve(_queryInsert.columns);
                })
                .catch(defer.reject);
            return defer.promise;
        }

        function find(userId) {
            var defer = $q.defer();

            DAO.query("SELECT * FROM User WHERE id='" + userId + "'").then(function (data) {
                if (data.rows.length) {
                    defer.resolve(data.rows[0]);
                } else {
                    new Parse
                        .Query('User')
                        .equalTo('objectId', userId)
                        .first()
                        .then(function (resp) {
                            // console.log(resp);
                            cacheUser(resp).then(function (user) {
                                defer.resolve(user);
                            })
                        });
                }
            })

            return defer.promise;
        }

        function profile(userId) {
            var defer = $q.defer();

            find(userId)
                .then(function (resp) {
                    console.log(resp);
                    var user = loadProfile(resp);

                    new Parse
                        .Query('Gallery')
                        .equalTo('user', resp)
                        .count()
                        .then(function (gallery) {
                            user.galleries = gallery;

                            new Parse
                                .Query('UserFollow')
                                .equalTo('user', resp)
                                .count()
                                .then(function (foloow) {
                                    user.follow = foloow;

                                    new Parse
                                        .Query('UserFollow')
                                        .equalTo('follow', resp)
                                        .count()
                                        .then(function (follow2) {
                                            user.follow2 = follow2;
                                            defer.resolve(user);
                                        });

                                });
                        });
                });

            return defer.promise;
        }

        function isFollow(userId) {
            var defer = $q.defer();

            console.log('isFollow start', userId);

            find(userId)
                .then(function (followUser) {
                    new Parse
                        .Query('UserFollow')
                        .equalTo('user', Parse.User.current())
                        .equalTo('follow', followUser)
                        .count()
                        .then(function (respFollow) {
                            //console.log('isFollow', respFollow);
                            defer.resolve(respFollow);
                        });
                });

            return defer.promise;
        }


        function getFollowers(userId) {
            var defer = $q.defer();

            if (!userId) {
                userId = Parse.User.current().id;
            }

            find(userId)
                .then(function (user) {
                    new Parse
                        .Query('UserFollow')
                        .equalTo('follow', user)
                        .count()
                        .then(function (qtdFollowers) {
                            defer.resolve(qtdFollowers);
                        }, function (err) {
                            defer.reject(err);
                        });
                });
            return defer.promise;
        }

        function getFollowing(userId) {
            var defer = $q.defer();

            if (!userId) {
                userId = Parse.User.current().id;
            }

            find(userId)
                .then(function (user) {
                    new Parse
                        .Query('UserFollow')
                        .equalTo('user', user)
                        .count()
                        .then(function (qtdFollowing) {
                            defer.resolve(qtdFollowing);
                        }, function (err) {
                            defer.reject(err);
                        });
                });
            return defer.promise;
        }

        function follow(status, user) {
            var defer     = $q.defer();
            var qtdFollow = Parse.User.current().qtdFollow ? parseInt(Parse.User.current().qtdFollow) : 0;


            find(user.id)
                .then(function (follow) {

                    if (status) {
                        // Follow User
                        console.log('Follow User', follow);

                        var Object = Parse.Object.extend('UserFollow');
                        var item   = new Object();

                        item.set('user', Parse.User.current());
                        item.set('follow', follow);
                        item.save()
                            .then(function (resp) {
                                console.log('Follow User', resp);

                                update({
                                    qtdFollow: qtdFollow + 1
                                })
                                    .then(function (userResp) {
                                        console.log('Follow User Update', userResp);
                                        defer.resolve(userResp);
                                    });

                            }, function (err) {
                                defer.resolve(err);
                            });
                    } else {
                        // Unfollow User
                        console.log('Unfollow User', follow);

                        new Parse
                            .Query('UserFollow')
                            .equalTo('user', Parse.User.current())
                            .equalTo('follow', follow)
                            .first()
                            .then(function (item) {
                                item
                                    .destroy()
                                    .then(function (resp) {

                                        update({
                                            qtdFollow: qtdFollow - 1
                                        })
                                            .then(function (userResp) {
                                                console.log('Follow User Update', userResp);
                                                defer.resolve(userResp);
                                            });
                                    }, function (err) {
                                        defer.resolve(err);
                                    });
                            });
                    }
                });

            return defer.promise;
        }

        function addFollows(users) {
            console.log('addFollows', users);
            var promises = [];
            angular.forEach(users, function (user) {
                promises.push(follow(true, user.id));
            });
            return $q.all(promises);
        }

        function getMail(email) {
            var defer = $q.defer();
            Loading.start();
            new Parse
                .Query('User')
                .equalTo('email', email)
                .first()
                .then(function (resp) {
                    Loading.end();
                    defer.resolve(resp);
                }, function (resp) {
                    Loading.end();
                    defer.reject(resp);
                });
            return defer.promise;
        }

        function loginFacebook(response) {
            var defer = $q.defer();

            var data = new Date(new Date().getTime() + response['authResponse']['expiresIn'] * 1000);

            Parse.FacebookUtils.logIn({
                id: response['authResponse']['userID'],
                access_token: response['authResponse']['accessToken'],
                expiration_date: data
            }, {
                success: function (response) {
                    // Funo caso tenha logado tanto no face quanto no Parse
                    var user = loadProfile(response);
                    console.log('User', user);
                    defer.resolve(user);
                }
            });

            return defer.promise;
        }

        function facebookLink() {
            var defer = $q.defer();

            facebook
                .login(['email'])
                .then(function (response) {

                        console.log('facebook login', response);
                        //Pega o Status do Login

                        var data = new Date(new Date().getTime() + response['authResponse']['expiresIn'] * 1000);

                        var user = Parse.User.current();
                        console.log(user, response, data);

                        Parse.FacebookUtils.link(user, {
                            id: response['authResponse']['userID'],
                            access_token: response['authResponse']['accessToken'],
                            expiration_date: data
                        }, {
                            success: function (user) {
                                // Funo caso tenha logado tanto no face quanto no Parse
                                console.log('User', user);
                                user.set('facebook', response['authResponse']['userID']);
                                user.set('facebookimg', 'https://graph.facebook.com/' + response['authResponse']['userID'] +
                                    '/picture?width=250&height=250');
                                user.set('facebook_complete', Boolean(true));
                                user.save()
                                    .then(function (response) {
                                        var user = loadProfile(response);
                                        console.info('User Update', user);
                                        defer.resolve(user);
                                    });
                            }
                        });
                    },
                    function (response) {
                        alert(JSON.stringify(response));

                    });


            return defer.promise;
        }


    }
})();

