(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name photogramComment
   *
   * @description
   * _Please update the description and restriction._
   *
   * @restrict A
   * */

  angular
    .module('app.photogram')
    .directive('photogramComment', photogramCommentDirective);

  function photogramCommentDirective($ionicModal, Loading, $ionicPopup, User, Notify, $timeout,
    AppConfig, Photogram, PhotogramForm) {

    var path = AppConfig.path;

    return {
      restrict: 'A',
      scope: {
        ngModel: '='
      },
      link: function (scope, elem) {


        elem.bind('click', openModalComment);

        function init() {
          scope.currentUser = User.currentUser();
          scope.nocomments = false;
          scope.loading = false;
          scope.form = {
            galleryId: scope.ngModel.id,
            text: ''
          };
        }

        function openModalComment() {
          console.log(scope.ngModel);

          init();

          scope.formFields = PhotogramForm.formComment;
          scope.submitComment = submitComment;
          scope.deleteComment = deleteComment;
          scope.editComment = editComment;
          scope.closeModal = function () {
            scope.modal.hide();
            scope.modal.remove();
          };

          scope.comments = scope.ngModel.comments;
          $timeout(function () {
            if (scope.comments.length === 0) {
              scope.nocomments = true;
            }
          }, 500);


          $ionicModal.fromTemplateUrl(path + '/directive/comment/photogram.comment.directive.html', {
              scope: scope,
              focusFirstInput: true
            })
            .then(function (modal) {
              scope.modal = modal;
              scope.modal.show();

            });
        }

        function deleteComment(obj) {
          console.log(obj);
          Notify
            .confirm(('Delete comment'), ('You are sure?'))
            .then(function (resp) {
              console.log(resp);
              if (resp) {
                Photogram
                  .deleteComment(obj)
                  .then(function (resp) {
                    console.log(resp);
                    getComments();
                  });
              }
            });
        }

        function editComment(obj) {
          console.log(obj);
          // An elaborate, custom popup
          scope.data = angular.copy(obj);
          $ionicPopup
            .show({
              template: '<input type="text" ng-model="data.text">',
              title: ('Edit comment'),
              //subTitle: 'Please use normal things',
              scope: scope,
              buttons: [{
                text: ('Cancel')
              }, {
                text: '<b>OK</b>',
                type: 'button-positive',
                onTap: function (e) {
                  console.log(scope.data);
                  if (!scope.data.text) {
                    //don't allow the user to close unless he enters wifi password
                    e.preventDefault();
                  } else {
                    return scope.data;
                  }
                }
              }]
            })
            .then(function (resp) {
              console.log(resp);
              if (resp) {
                Photogram
                  .updateComment(resp)
                  .then(function (resp) {
                    console.log(resp);
                    getComments();
                  });
              }
            });
        }

        function getComments() {
          scope.loading = true;
          Photogram
            .getComments(scope.ngModel.id)
            .then(function (resp) {
              scope.comments = resp;
              scope.ngModel.comments = resp;
              scope.loading = false;
            });
        }

        function submitComment(rForm, form) {
          if (rForm.$valid) {
            var dataForm = angular.copy(form);
            Loading.start();
            Photogram
              .addComment(dataForm)
              .then(function (resp) {
                console.log(resp);
                getComments();
                Loading.end();
                scope.closeModal();
              });
          }
        }


      }
    };
  }

})();
(function () {
    'use strict';

    /**
     * @ngdoc directive
     * @name facebookLogin
     *
     * @description
     * _Please update the description and restriction._
     *
     * @restrict A
     * */

    angular
        .module('app.photogram')
        .directive('facebookLogin', facebookLoginDirective);

    function facebookLoginDirective(Loading, User, $state, $cordovaFacebook, $facebook, AppConfig, Notify) {
        return {
            restrict: 'E',
            link: facebookLoginLink,
            template: '<button class="button button-block button-facebook"><i class="icon ion-social-facebook"></i> <span>{{ \'USER.BUTTON.LOGIN_SOCIAL\'| translate}} {{name || \'Facebook\'}}</span> </button>',
        };

        function facebookLoginLink(scope, elem, attr) {

            var facebook = window.cordova ? $cordovaFacebook : $facebook;

            console.log('Facebook login', facebook);
            
            facebook
                .getLoginStatus()
                .then(function (resp) {
                    console.log(resp);
                    facebook
                        .api('/me')
                        .then(function (profile) {
                            console.log(profile);
                            scope.name = profile.name;
                        });
                })
                .catch(function (resp) {
                    console.log('Not logged');
                });

            elem.bind('click', facebookLogin);

            function facebookLogin() {
                Loading.start();
                User
                    .facebookLogin()
                    .then(function (resp) {
                        console.log(resp);

                        Loading.end();
                        switch (resp.status) {
                            case 0:
                                // logado
                                $state.go(AppConfig.routes.home, {
                                    clear: true
                                });
                                break;
                            case 1:
                                // novo user
                                $state.go('user.avatar', {
                                    clear: true
                                });
                                break;
                            case 2:
                                // merge
                                $state.go('user.merge', {
                                    clear: true
                                });
                                break;
                        }
                    })
                    .catch(function () {
                        Loading.end();
                        Notify.alert({
                            title: 'Ops',
                            text: ('Facebook error')
                        });
                    });
            }

        }
    }

})();
(function () {

    angular
        .module('app.photogram')
        .factory('PhotogramFeedback', PhotogramFeedbackFactory);

    function PhotogramFeedbackFactory($q, AppConfig, Photogram, $rootScope, PhotogramFeedbackForm, $ionicModal, Notify) {
        var path = AppConfig.path;

        return {
            modal: modal,
            submit: submit
        };
        function modal(gallery) {
            var $scope            = $rootScope.$new();
            $scope.formFields = PhotogramFeedbackForm.form;
            $scope.submitFeedback = submitFeedback;
            $scope.closeModal     = closeModal;
            $scope.form           = {
                photogramId: gallery.id,
                subject: 'complaint'
            };

            function submitFeedback() {
                var dataForm = angular.copy($scope.form);
                submit(dataForm)
                    .then(function (resp) {
                        console.log(resp);
                        closeModal();
                    });
            }


            function closeModal() {
                $scope.modal.hide();
                $scope.modal.remove();
                delete $scope.modal;
            }

            $ionicModal
                .fromTemplateUrl(path + '/directive/feedback/feedback-modal.html', {
                    scope: $scope,
                    focusFirstInput: true
                })
                .then(function (modal) {
                    $scope.modal = modal;
                    $scope.modal.show();
                });
        }


        function submit(form) {
            var defer = $q.defer();

            console.log(form);

            Photogram
                .find(form.photogramId)
                .then(function (Photogram) {
                    console.log(Photogram);
                    var Object = Parse.Object.extend('PhotogramFeedback');
                    var item   = new Object();

                    delete form.PhotogramId;

                    angular.forEach(form, function (value, key) {
                        item.set(key, value);
                    });

                    item.set('user', Parse.User.current());
                    item.set('Gallery', Photogram);

                    item
                        .save(null)
                        .then(function (resp) {
                            Notify.alert({
                                title: ('Thanks'),
                                text: ('Thanks for your Feedback')
                            });
                            defer.resolve(resp);
                        });
                });


            return defer.promise;
        }

    }



})();

