(function () {
  'use strict';
  angular
    .module('app.photogram')
    .directive('photogramPhotoCapture', photogramPhotoCaptureDirective);

  function photogramPhotoCaptureDirective($ionicModal, $cordovaGeolocation, AppConfig, Loading, $state, PhotoService,
    Photogram) {

    var path = AppConfig.path;

    return {
      restrict: 'A',
      scope: {
        photogram: '@'
      },
      transclude: false,
      link: photogramPhotoCaptureLink
    };

    function photogramPhotoCaptureLink(scope, elem, attr) {

      scope.getGeo = getGeo;
      scope.formFields = Photogram.form;
      scope.open = open;
      scope.submitCapture = submitCapture;
      scope.closeModal = closeModal;
      elem.bind('click', openModalCapture);

      function init() {
        scope.loading = true;
        scope.comments = [];
        Photogram
          .getComments(scope.photogram)
          .then(function (resp) {
            scope.comments = resp;
            scope.loading = false;
          });

        scope.form = {
          photogramId: scope.photogram,
          text: ''
        };
      }

      function openModalCapture() {
        console.log(scope.photogram);


        $ionicModal.fromTemplateUrl(path + '/view/photogram.capture.modal.html', {
          scope: scope
        }).then(function (modal) {
          scope.modal = modal;
          scope.modal.show();
        });
      }


      function init() {
        scope.active = false;
        scope.form = {
          title: '',
          location: '',
          photo: '',
          geo: false
        };

        scope.map = {
          center: {
            latitude: -23.5333333,
            longitude: -46.6166667
          },
          scrollwheel: false,
          zoom: 15
        };
        scope.data = '';
        scope.loading = false;

      }

      function getLocation() {
        var posOptions = {
          timeout: 10000,
          enableHighAccuracy: false
        };

        if (scope.form.location === '') {

          scope.loading = false;

          $cordovaGeolocation
            .getCurrentPosition(posOptions)
            .then(function (position) {

              scope.here = {
                id: 1,
                coords: {
                  latitude: position.coords.latitude,
                  longitude: position.coords.longitude,
                },
                icon: 'img/pin.png'
              };

              scope.form.location = {
                latitude: position.coords.latitude,
                longitude: position.coords.longitude,
              };

              scope.map.center = scope.here.coords;
              scope.loading = false;

              console.log(scope.form);
              console.log(scope.here);
              console.log(position);
            }, function (err) {
              // error
            });
        }
      };

      function getGeo(resp) {
        if (resp) getLocation();
      }

      function open() {
        init();
        PhotoService
          .open()
          .then(function (resp) {
            scope.loading = false;
            scope.active = true;
            scope.form.photo = resp;
            scope.data = 'data:image/jpeg;base64,' + resp;
            // angular.element ('.title').focus ();
          })
          .catch(function () {
            $state.go('photogram.home');
          });
      }

      function submitCapture() {

        var canvas = window.document.getElementById('image');
        var dataUrl = canvas.toDataURL();
        var dataForm = angular.copy(scope.form);

        console.log('submit', dataUrl);

        return false;

        Loading.start();
        Photogram
          .add(dataForm)
          .then(function (resp) {
            $state.go('photogram.home', {
              reload: true
            });
            init();
            Loading.end();
          });
      }

      function closeModal() {
        scope.modal.hide();
        scope.modal.remove();
      }
    }
  }

})();

(function () {
    'use strict';
    angular
        .module('app.photogram')
        .directive('photogramPhotoGrid', photogramPhotoGridDirective);

    function photogramPhotoGridDirective(AppConfig, Photogram, $ionicPopup, PhotogramShare, PhotogramFeedback, $ionicActionSheet) {
        var path = AppConfig.path;

        return {
            restrict: 'E',
            scope: {
                user: '=',
                profile: '='
            },
            templateUrl: path + '/directive/photogrid/photo-grid.html',
            link: photogramPhotoGridCtrl
        };

        function photogramPhotoGridCtrl($scope, elem, attr) {

            $scope.action  = action;
            $scope.like    = likePhoto;
            $scope.loading = true;
            $scope.feed    = feed;
            $scope.gallery = {
                src: ''
            };

            init();

            $scope.$on('PhotogramHome:reload', function () {
                feed(true);
            });

            function init() {
                $scope.data  = {
                    total: -1,
                    galleries: []
                };
                $scope.page  = 0;
                $scope.empty = false;
                $scope.more  = false;
                feed(false,true);
            }


            function feed(force, cache) {
                // console.log('Load ');

                if (force) {
                    init();
                }

                var service = Photogram.getOfflineFeed($scope.page, $scope.user);

                if (!cache) {
                    service = Photogram.feedGrid($scope.page, $scope.user)
                }

                service
                    .then(function (resp) {

                        console.log(resp);
                        resp.rows.map(function (item) {
                            item.progress     = false;
                            $scope.data.total = resp.total;
                            $scope.data.galleries.push(item);
                        });

                        // console.log('qtd', resp.length);

                        if (resp.length) {
                            $scope.more = true;
                            $scope.page++;
                        } else {
                            $scope.empty = true;
                            $scope.more  = false;
                        }
                    })
                    .then(function () {
                        $scope.loading = false;
                        $scope.$broadcast('scroll.refreshComplete');
                        $scope.$broadcast('scroll.infiniteScrollComplete');

                    })
                    .catch(function () {
                        $scope.$broadcast('scroll.refreshComplete');
                        $scope.$broadcast('scroll.infiniteScrollComplete');
                        if ($scope.data.length) {
                            $scope.loading = false;
                            $scope.page++;
                        } else {
                            $scope.empty   = true;
                            $scope.loading = false;
                        }
                        $scope.more = false;
                    });
            }

            function likePhoto(gallery) {
                gallery.item.liked = !gallery.item.liked;
                Photogram
                    .likeGallery(gallery.id)
                    .then(function (resp) {
                        gallery.item.qtdLike = resp.likes;
                    });
            }

            function action(gallery) {

                var buttons = [{
                    text: '<i class="icon ion-share"></i>' + ('Share')
                }, {
                    text: '<i class="icon ion-alert-circled"></i>' + ('Report')
                }];

                var user = Parse.User.current();

                if (user.id === gallery.user.id) {
                    var buttonDelete = {
                        text: '<i class="icon ion-trash-b"></i>' + ('Delete your photo')
                    };
                    buttons.push(buttonDelete);
                }
                var message     = {
                    image: gallery.src,
                    text: gallery.item.title
                }
                var actionSheet = {
                    buttons: buttons,
                    titleText: ('Photo'),
                    cancelText: ('Cancel'),
                    buttonClicked: actionButtons
                };


                function actionButtons(index) {
                    switch (index) {
                        case 0:
                            PhotogramShare.share(message);
                            break;
                        case 1:
                            PhotogramFeedback.modal(gallery);
                            break;
                        case 2:

                            $ionicPopup
                                .confirm({
                                    title: ('Delete photo'),
                                    template: ('Are you sure?')
                                })
                                .then(function (res) {
                                    if (res) {
                                        Photogram
                                            .deletePhoto(gallery.id)
                                            .then(function () {
                                                console.log('Photo deleted');
                                                $scope.$emit('PhotogramHome:reload');
                                            });
                                    }
                                });


                    }
                    return true;
                }

                // Show the action sheet
                $ionicActionSheet.show(actionSheet);
            }
        }

    }


})();

