(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name photogramProfile
   *
   * @description
   * _Please update the description and restriction._
   *
   * @restrict A
   * */

  angular
    .module('app.photogram')
    .directive('profileModal', profileModalDirective);

  function profileModalDirective($ionicModal, $rootScope, $q, Photogram, User) {

    return {
      restrict: 'A',
      scope: {
        user: '='
      },
      link: profileModalLink
    };

    function profileModalLink(scope, elem) {

      elem.bind('click', openModal);

      function init() {
        var defer = $q.defer();
        scope.loadingPhotogram = true;

        // console.log(scope.user);
        
        scope.avatar = User.avatar(scope.user.attributes);

        Photogram
          .getUserGallery(scope.user.id)
          .then(function (resp) {
            scope.data = resp;
            console.log(resp);
            scope.$broadcast('scroll.refreshComplete');
            scope.$broadcast('scroll.infiniteScrollComplete');
            scope.loadingPhotogram = false;
            defer.resolve(scope.data);
          });

        return defer.promise;
      }

      function changeTab(tab) {
        if (tab === 'list') {
          scope.tab = {
            list: true,
            grid: false
          };
        } else {
          scope.tab = {
            list: false,
            grid: true
          };
        }
      }

      function getFollower(userId) {
        console.log('getFollowers', userId);
        scope.loadingFollowers = true;
        scope.loadingFollowing = true;
        scope.loadingPhotos = true;

        Photogram
            .getUserGalleryQtd(userId)
            .then(function (qtdPhotos) {
              console.log(qtdPhotos);
              scope.user.qtdPhotos = qtdPhotos;
              scope.loadingPhotos  = false;
            });

        User
            .getFollowers(userId)
            .then(function (qtdFollowers) {
              console.log('qtdFollower: seguindo', qtdFollowers);
              scope.user.qtdFollowers = qtdFollowers;
              scope.loadingFollowers  = false;
            });

        User
            .getFollowing(userId)
            .then(function (qtdFollowing) {
              console.log('qtdFollowing: seguidores', qtdFollowing);
              scope.user.qtdFollowing = qtdFollowing;
              scope.loadingFollowing  = false;
            });
      }

      function openModal() {

        // console.log(scope.user);
        
        $ionicModal
          .fromTemplateUrl('app/module/photogram/directive/profile-modal/profile-modal.html', {
            scope: scope
          })
          .then(function (modal) {
            scope.modalProfile = modal;
            scope.loadingFollow = true;
            scope.changeTab = changeTab;
            scope.follow = follow;
            scope.closeModal = closeModal;
            scope.modalProfile.show();

            init();
            getFollower(scope.user.id);
            changeTab('list');
            isFollow();

            function isFollow() {
              User
                .isFollow(scope.user.id)
                .then(isFollowResp);
            }

            function isFollowResp(resp) {
              console.info('follow user?', resp);
              scope.user.follow = resp;
              scope.loadingFollow = false;
            }

            function follow() {

              scope.loadingFollow = true;
              var status;

              if (scope.user.follow) {
                status = false;
              } else {
                status = true;
              }

              User
                .follow(status, scope.user)
                .then(followResp);

              function followResp(resp) {

                console.log('Follow result', resp);
                scope.user.follow = status;
                scope.loadingFollow = false;
                getFollower(scope.user.id);
              }
            }

            function closeModal() {
              delete scope.data;
              scope.modalProfile.hide();
              scope.modalProfile.remove();
            }
          });
      }
    }
  }

})();
(function () {
    'use strict';
    angular
        .module('app.photogram')
        .directive('ionSearch', ionSearchDirective);

    function ionSearchDirective($timeout) {

        return {
            restrict: 'E',
            scope: {
                getData: '&source',
                ngModel: '=?',
                search: '=?filter'
            },
            templateUrl: 'app/module/photogram/directive/search/search-directive.html',
            link: function (scope, element, attrs) {
                attrs.minLength   = attrs.minLength || 0;
                scope.placeholder = attrs.placeholder || '';
                scope.clear = function  () {
                    console.log('Limpar campo');
                    scope.input = '';
                    scope.$apply();
                };

                if (attrs.class) {
                    element.addClass(attrs.class);
                }

                if (attrs.source) {
                    scope.$watch('search.value', watchSearch);
                }

                function watchSearch(newValue) {
                    if (newValue.length > attrs.minLength) {
                        $timeout(function () {
                            scope
                                .getData({
                                    str: newValue
                                })
                                .then(function (results) {
                                    scope.ngModel = results;
                                });
                        }, 1000);
                    } else {
                        scope.ngModel = [];
                    }
                }

            }

        };
    }


})();

(function () {
  'use strict';
  angular
    .module('app.photogram')
    .directive('settingsModal', settingsModalDirective);

  function settingsModalDirective($ionicModal, $translate, AppConfig, $cordovaInAppBrowser, $rootScope, PhotogramShare,
    User,
    UserForm, $state) {

    var path = AppConfig.path;

    return {
      restrict: 'A',
      scope: {
        photogram: '@'
      },
      template: '',
      link: function (scope, elem) {

        elem.bind('click', openModal);
        scope.closeModal = closeModal;
        scope.link = link;
        scope.openLink = openLink;
        scope.changeLanguage = changeLanguage;
        scope.share = PhotogramShare.share;

        function init() {
          scope.form = User.currentUser();
          scope.formFields = UserForm.profile;
          scope.languages = AppConfig.locales;
          scope.language = $translate.use();
        }

        function openModal() {

          init();
          $ionicModal.fromTemplateUrl(path + '/directive/settings-modal/settings-modal.html', {
            scope: scope
          }).then(function (modal) {
            scope.modal = modal;
            scope.modal.show();
          });
        }

        function link(sref) {
          $state.go(sref);
          scope.closeModal();
        }

        function openLink(url) {
          var options = {
            location: 'yes',
            clearcache: 'yes',
            toolbar: 'yes'
          };

          $cordovaInAppBrowser.open(url, '_blank', options);
        }

        function changeLanguage(language) {
          $translate.use(language);
          moment.locale(language);
          scope.form.language = language;
          submitUpdateProfile(scope.form);
        }


        function submitUpdateProfile(form) {
          var dataForm = angular.copy(form);
          User
            .update(dataForm)
            .then(function (resp) {
              console.log(resp);
              init();
              scope.closeModal();
            });
        }

        function closeModal() {
          scope.modal.hide();
          scope.modal.remove();
        }

      }
    };
  }


})();

